body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  flex-direction: column;
  color: #012443;
  background-color: #9596d1;
}

#box {
  background-color: #d8c7ff;
  padding: 15px;
  width: 400px;
  border-radius: 5px;
  box-shadow: 0px 0px 20px #012443;
}

input {
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  border-radius: 2px;
  border: none;
  margin: 3px 0;
  outline: none;
  font-size: 14px;
}

#button {
  display: flex;
  justify-content: flex-end;
}

#button button {
  background-color: transparent;
  color: #fff;
  border: none;
  background-color: #012443;
  display: block;
  width: 125px;
  padding: 6px 20px;
  margin: 10px 0 0 0;
  border-radius: 3px;
}

ul li {
  list-style-type: none;
}

h2 {
  text-align: left;
}

#list {
  width: 100%;
  padding: 0 10px;
  margin: 10px 0;
  box-sizing: border-box;
  transition: all linear 300ms;
}

#list li {
  width: 100%;
  display: flex;
  margin: 5px 0;
  justify-content: space-between;
  font-weight: 500;
}

#totalCount {
  font-weight: 500;
  margin: 10px 0;
  text-align: right;
  padding: 0 10px;
  display: block;
}

#footer { 
  margin-top: 50px;
}

#footer a {
  text-decoration: none;
  color: #012443;
  font-weight: 800;
}